"use client"

import { useCallback, useEffect, useMemo, useState } from "react"
import { AlertCircle, Loader2 } from "lucide-react"

import { DashboardShell } from "@/components/navigation/dashboard-shell"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { useToast } from "@/components/ui/use-toast"
import { RequestCategoryBadge } from "@/components/request-category-badge"
import { RequestStatusBadge } from "@/components/request-status-badge"
import { authService, type AdminTeamSummary } from "@/lib/auth-service"
import { requestsService, type RequestsListResponse } from "@/lib/requests-service"

const REQUESTS_ORDERING = "-created_at"

export default function RequestsTrackingPage() {
  const { toast } = useToast()
  const [authorized, setAuthorized] = useState<boolean | null>(null)
  const [teams, setTeams] = useState<AdminTeamSummary[]>([])
  const [selectedTeamId, setSelectedTeamId] = useState<string>("")
  const [loadingTeams, setLoadingTeams] = useState(true)
  const [teamError, setTeamError] = useState<string | null>(null)

  const [requestsPage, setRequestsPage] = useState(1)
  const [requestsResponse, setRequestsResponse] = useState<RequestsListResponse | null>(null)
  const [requestsLoading, setRequestsLoading] = useState(false)
  const [requestsError, setRequestsError] = useState<string | null>(null)

  const loadTeams = useCallback(async () => {
    setLoadingTeams(true)
    setTeamError(null)
    try {
      const currentUser = authService.getCurrentUser()
      if (!currentUser?.is_admin) {
        setAuthorized(false)
        setTeams([])
        setLoadingTeams(false)
        return
      }

      setAuthorized(true)
      const data = await authService.getAdminTeams()
      setTeams(data)
      setSelectedTeamId((prev) => (prev && data.some((team) => team.id === prev) ? prev : ""))
    } catch (error) {
      const message = error instanceof Error ? error.message : "Unable to load teams."
      setTeamError(message)
      toast({ variant: "destructive", title: "Failed to load teams", description: message })
    } finally {
      setLoadingTeams(false)
    }
  }, [toast])

  const loadRequests = useCallback(
    async (teamId: string, page: number) => {
      if (!teamId) {
        setRequestsResponse(null)
        return
      }
      setRequestsLoading(true)
      setRequestsError(null)
      try {
        const response = await requestsService.listTeamRequests(teamId, { page, ordering: REQUESTS_ORDERING })
        setRequestsResponse(response)
      } catch (error) {
        const message = error instanceof Error ? error.message : "Unable to load requests."
        setRequestsError(message)
      } finally {
        setRequestsLoading(false)
      }
    },
    [],
  )

  useEffect(() => {
    void loadTeams()
  }, [loadTeams])

  useEffect(() => {
    setRequestsPage(1)
    if (selectedTeamId) {
      void loadRequests(selectedTeamId, 1)
    } else {
      setRequestsResponse(null)
    }
  }, [selectedTeamId, loadRequests])

  const handleTeamChange = useCallback((value: string) => {
    setSelectedTeamId(value)
  }, [])

  const teamOptions = useMemo(
    () => teams.map((team) => ({ id: team.id, label: team.name, subtitle: team.institution })),
    [teams],
  )

  const requestResults = requestsResponse?.results ?? []
  const hasNext = Boolean(requestsResponse?.next)
  const hasPrevious = Boolean(requestsResponse?.previous)

  const selectedTeam = useMemo(
    () => (selectedTeamId ? teams.find((team) => team.id === selectedTeamId) ?? null : null),
    [selectedTeamId, teams],
  )

  return (
    <DashboardShell>
      <div className="space-y-8">
        {authorized === false && (
          <Card className="border border-destructive/30 bg-destructive/5 text-destructive">
            <CardHeader className="pb-3">
              <CardTitle className="flex items-center gap-2 text-sm font-semibold">
                <AlertCircle className="h-4 w-4" /> Access restricted
              </CardTitle>
              <CardDescription className="text-xs text-destructive/90">
                Only admin users can review team request pipelines.
              </CardDescription>
            </CardHeader>
          </Card>
        )}

        <header className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
          <div className="space-y-1">
            <h1 className="text-2xl font-bold tracking-tight" style={{ color: "#002449" }}>
              Requests Tracking
            </h1>
            <p className="text-sm text-muted-foreground">
              Pick a team to see every BOM, additional, and fabrication request in one place.
            </p>
          </div>
          <div className="flex flex-col gap-2 md:items-end">
            <Select
              value={selectedTeamId || undefined}
              onValueChange={handleTeamChange}
              disabled={!teams.length || loadingTeams}
            >
              <SelectTrigger className="w-64">
                <SelectValue placeholder={loadingTeams ? "Loading teamsâ€¦" : "Select a team"} />
              </SelectTrigger>
              <SelectContent>
                {teamOptions.map((team) => (
                  <SelectItem key={team.id} value={team.id}>
                    <div className="flex flex-col text-sm">
                      <span className="font-semibold" style={{ color: "#002449" }}>
                        {team.label}
                      </span>
                      <span className="text-xs text-muted-foreground">{team.subtitle}</span>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            <div className="flex items-center gap-2 text-xs text-muted-foreground">
              <span>{teams.length} team{teams.length === 1 ? "" : "s"} loaded</span>
              <Button
                variant="outline"
                size="xs"
                className="gap-1"
                onClick={() => void loadTeams()}
                disabled={loadingTeams}
              >
                {loadingTeams ? <Loader2 className="h-3.5 w-3.5 animate-spin" /> : "Refresh"}
              </Button>
            </div>
          </div>
        </header>

        {teamError && (
          <Card className="border border-destructive/30 bg-destructive/5 text-destructive">
            <CardHeader className="pb-3">
              <CardTitle className="flex items-center gap-2 text-sm font-semibold">
                <AlertCircle className="h-4 w-4" /> Unable to load teams
              </CardTitle>
              <CardDescription className="text-xs text-destructive/90">{teamError}</CardDescription>
            </CardHeader>
          </Card>
        )}

        {authorized === false ? null : selectedTeamId === "" ? (
          <Card className="border border-dashed border-muted/50">
            <CardHeader className="pb-4 text-center">
              <CardTitle className="flex flex-col items-center gap-2 text-base font-semibold" style={{ color: "#002449" }}>
                Select a team to continue
              </CardTitle>
              <CardDescription className="text-xs text-muted-foreground">
                The request history will appear once a team is chosen.
              </CardDescription>
            </CardHeader>
          </Card>
        ) : (
          <Card className="border border-gray-200 shadow-sm">
            <CardHeader className="flex flex-col gap-2 md:flex-row md:items-center md:justify-between">
              <div>
                <CardTitle className="text-lg font-semibold" style={{ color: "#002449" }}>
                  {selectedTeam?.name || "Team requests"}
                </CardTitle>
                <CardDescription className="text-xs text-muted-foreground">
                  Results ordered by most recent activity.
                </CardDescription>
              </div>
              <div className="flex items-center gap-2">
                <Button
                  variant="outline"
                  size="sm"
                  className="gap-2"
                  onClick={() => void loadRequests(selectedTeamId, requestsPage)}
                  disabled={requestsLoading}
                >
                  {requestsLoading ? <Loader2 className="h-4 w-4 animate-spin" /> : "Refresh"}
                </Button>
                <span className="text-xs text-muted-foreground">
                  Page {requestsPage}
                </span>
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              {requestsError ? (
                <div className="flex items-center gap-2 rounded-md border border-destructive/30 bg-destructive/5 px-3 py-2 text-sm text-destructive">
                  <AlertCircle className="h-4 w-4" /> {requestsError}
                </div>
              ) : requestsLoading && !requestResults.length ? (
                <div className="flex min-h-[200px] items-center justify-center text-muted-foreground">
                  <Loader2 className="h-5 w-5 animate-spin" />
                </div>
              ) : requestResults.length === 0 ? (
                <div className="text-sm text-muted-foreground">No requests found for this team yet.</div>
              ) : (
                <div className="space-y-4">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead>Category</TableHead>
                        <TableHead>Status</TableHead>
                        <TableHead>Submitted</TableHead>
                        <TableHead>Last Updated</TableHead>
                        <TableHead>Details</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {requestResults.map((request) => (
                        <TableRow key={request.id}>
                          <TableCell>
                            <RequestCategoryBadge category={request.category} />
                          </TableCell>
                          <TableCell>
                            <RequestStatusBadge status={request.status} />
                          </TableCell>
                          <TableCell className="text-xs text-muted-foreground">
                            {new Date(request.created_at).toLocaleString()}
                          </TableCell>
                          <TableCell className="text-xs text-muted-foreground">
                            {new Date(request.updated_at).toLocaleString()}
                          </TableCell>
                          <TableCell className="text-xs text-muted-foreground max-w-[280px]">
                            {request.description || request.notes || "â€”"}
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                  <div className="flex items-center justify-between text-xs text-muted-foreground">
                    <span>Total: {requestsResponse?.count ?? 0}</span>
                    <div className="flex items-center gap-2">
                      <Button
                        variant="outline"
                        size="xs"
                        onClick={() => {
                          const nextPage = Math.max(requestsPage - 1, 1)
                          setRequestsPage(nextPage)
                          void loadRequests(selectedTeamId, nextPage)
                        }}
                        disabled={requestsLoading || !hasPrevious}
                      >
                        Previous
                      </Button>
                      <Button
                        variant="outline"
                        size="xs"
                        onClick={() => {
                          const nextPage = requestsPage + 1
                          setRequestsPage(nextPage)
                          void loadRequests(selectedTeamId, nextPage)
                        }}
                        disabled={requestsLoading || !hasNext}
                      >
                        Next
                      </Button>
                    </div>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        )}
      </div>
    </DashboardShell>
  )
}
